import { Button, Checkbox, Container, Divider, FormControl, Grid, InputLabel, MenuItem, Select, TextField, Typography } from '@mui/material';
import { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import { useLocation, useNavigate } from 'react-router';
import * as Yup from 'yup';
import { useSettingsContext } from 'src/components/settings';
import { constants } from 'src/utils/constant';
import { decryptToken } from 'src/utils/common';
import axios, { endpoints } from 'src/utils/axios';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import { useAuthContext } from 'src/auth/hooks';
// sections
// ----------------------------------------------------------------------

export default function UserDetailsPage() {
    const { user: authUser } = useAuthContext();

    const settings = useSettingsContext();
    const navigate = useNavigate();
    const location = useLocation();
    const { enqueueSnackbar } = useSnackbar();
    const userData = location?.state;

    const [user, setUser] = useState({
        id: '',
        name: '',
        email: '',
        role: '',
        permissions: []
    });

    //  Create a YUP SCHEMA for the userData
    const schema = Yup.object().shape({
        name: Yup.string().required('Name is required').matches(/^[A-Za-z\s]+$/, 'Name must contain only letters and spaces'),
    });

    useEffect(() => {
        if (userData) {
            const data = Object.keys(userData.permissions).map((moduleName, index) => ({
                id: index + 1,
                moduleName,
                permissions: userData.permissions[moduleName],
            }));

            setUser((prev) => ({
                ...prev,
                id: userData._id,
                name: userData.name,
                email: userData.email,
                role: userData.roleId.name,
                permissions: data
            }));


        }

    }, [userData])


    const handleSubmit = async () => {
        try {
            const checkName = { name: user.name }
            await schema.validate(checkName, { abortEarly: false });
        } catch (error) {
            enqueueSnackbar('User name is invalid!', { variant: 'error' });
            return;
        }

        try {
            const res = await axios.post(endpoints.user.updateRolesAndPermissions, {
                name: user.name,
                role: user.role,
                permissions: user.permissions,
                id: user.id
            });
            enqueueSnackbar('User details updated!', { variant: 'success' });
            navigate(-1);
        } catch (error) {
            enqueueSnackbar(error.error || 'Something went wrong!', { variant: 'error' });
        }
    };

    const handleCheckboxChange = (event, id, type) => {
        const updatedData = user?.permissions.map(row => {
            if (row.id === id) {
                return {
                    ...row,
                    permissions: {
                        ...row.permissions,
                        [type]: event.target.checked
                    }
                };
            }
            return row;
        });
        setUser((prev) => ({ ...prev, permissions: updatedData }))
    };

    const columns = [
        { name: 'Module Name', selector: row => row.moduleName.charAt(0).toUpperCase() + row.moduleName.slice(1), width: '150px' },
        { name: 'View', selector: row => row.permissions.r, cell: (row) => <Checkbox disabled={user.role === 'ADMIN'} checked={row.permissions.r} value={row.permissions.r} onChange={(event) => handleCheckboxChange(event, row.id, 'r')} /> },
        { name: 'Edit', selector: row => row.permissions.u, cell: (row) => <Checkbox disabled={user.role === 'ADMIN'} checked={row.permissions.u} value={row.permissions.u} onChange={(event) => handleCheckboxChange(event, row.id, 'u')} /> }
    ];

    return (
        <>
            <Helmet>
                <title> Dashboard : User Details</title>
            </Helmet>

            <Container maxWidth={settings.themeStretch ? false : 'xl'}>
                <Typography variant="h4"> User Details </Typography>


                {/* User Profile Section */}
                <Grid container sx={{ p: 2 }} spacing={2} >
                    <Grid item xs={12}>
                        <Typography variant='h5'>
                            User Profile
                        </Typography>
                        <Typography variant='body2'>
                            Manage user details and Permissions here
                        </Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Divider />
                    </Grid>
                    <Grid item xs={3}>
                        <Typography variant='h6'>
                            User Details
                        </Typography>
                        <Typography variant='caption'>
                            Manage user details here
                        </Typography>
                    </Grid>
                    <Grid item xs={3}>
                        <TextField
                            label='Name'
                            fullWidth
                            value={user.name}
                            onChange={(e) => { setUser((prev) => ({ ...prev, name: e.target.value })) }}
                        />
                    </Grid>
                    <Grid item xs={3}>
                        <TextField
                            label='Email Address'
                            fullWidth
                            value={user.email}
                            disabled
                        />
                    </Grid>
                    <Grid item xs={3}>
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label">Role</InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={user.role}
                                onChange={(e) => { setUser((prev) => ({ ...prev, role: e.target.value })) }}
                                label="Role"
                            >
                                <MenuItem value='ADMIN'>ADMIN</MenuItem>
                                <MenuItem value='MANAGER'>MANAGER</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={12}>
                        <Divider />
                    </Grid>
                    <Grid item xs={3}>
                        <Typography variant='h6'>
                            User Access
                        </Typography>
                        <Typography variant='caption'>
                            Manage user permissions here
                        </Typography>
                    </Grid>
                    <Grid item xs={5}>
                        <Typography variant='h6'>
                            User Access
                        </Typography>
                        <DataTable
                            columns={columns}
                            data={user.permissions}
                        />
                    </Grid>

                </Grid>
                <Grid container spacing={2}>
                    <Grid item xs={12} display='flex' justifyContent='flex-end' gap='10px'>
                        <Button variant='outlined' onClick={() => {
                            setUser({
                                name: '',
                                email: '',
                                role: '',
                                permissions: []
                            });
                            navigate(-1);
                        }}>Cancel</Button>
                        {
                            authUser?.permissions?.user?.u &&
                            <Button variant='contained' color='primary' onClick={handleSubmit}>SUBMIT</Button>
                        }
                    </Grid>
                </Grid>

            </Container>
        </>
    );
}
